import React, { lazy, useState, useEffect } from 'react'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout,
  CDataTable
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {fieldviavalen} from './source.js'
import {ReactComponent as Satu} from './satu.svg'
import {ReactComponent as Satuoff} from './satuoff.svg'
import Aform from './aform.js'
import Bform from './bform.js'
import { Redirect, Route, Switch } from 'react-router-dom';

function LogoFormA(props) {
    const tab = props.tab;
    if (tab== "a") {
      return <Satu/>;
    }
    return <Satuoff/> ;
  }
function LogoFormB(props) {
    const tab = props.tab;
    if (tab=="b") {
      return <Satu/>;
    }
    return <Satuoff/> ;
  } 
function IsiUtama(props) {
    const tab = props.tab;
    if (tab=="a") {
      return <Aform kembali={(item) => props.kembali(item)} kirimInformasi={(item) => props.kirimInformasi(item)} informasi={props.informasi} certificate={props.certificate} kirimCertificate={(item) => props.kirimCertificate(item)} jehee={(item)=>props.jehee(item)} calleback={(item) => props.calleback(item)} />;
    }
    return <Bform defic={props.defic} kirimDefic={(item)=>props.kirimDefic(item)} juhuu={(item)=>props.juhuu(item)} Viavalen={(item)=> props.Viavalen(item)} kirimDefisiensi={(item)=>props.kirimDefisiensi(item)} defisiensi={props.defisiensi} calleback={(item) => props.calleback(item)} /> ;
  }      

const FormA = (props) => {
    const [tab, setTab] = useState("a");
    const [pancingan, setPancingan] = useState();
    const [certificate, setCertificate] = useState([{
        id:1,
        title:"",
        issuingauthority:"",
        dateofissue:"",
        dateofexpiry:""
    }]);
    const [informasi, setInformasi] = useState([{
        id:1,
        title:"",
        issuingauthority:"",
        dateofissue:"",
        dateofexpiry:""
    }]); 
    const [defisiensi, setDefisiensi] = useState([{
        id:1,
        title:"",
        issuingauthority:"",
        dateofissue:"",
        dateofexpiry:""
    }]); 
    const [defic, setDefic] = useState([
        {
            id:1,
            title:"",
            issuingauthority:"",
            dateofissue:"",
            dateofexpiry:""
        }
    ])
    const [viavalen, setViavalen] = useState("") 
    const [kembali, setKembali] = useState("")      
    const handleClickTabA = () => {
        if(tab!=="a"){
            setTab("a")
        }
    }
    const handleViavalen = (item) => {
        setViavalen(item)
        console.log(viavalen)
    }  
    const handleKembali = (item) => {
        setKembali(item)
        console.log("kembali", kembali)
    } 
    const handleCertificate = (item) => {
        setCertificate(item)
    }  
    const handleInformasi = (item) => {
        setInformasi(item)
    }   
    const handleDefisiensi = (item) => {
        setDefisiensi(item)
    }       
    const handleDefic = (item) => {
        setDefic(item)
    }     
    const handleSelanjutnya = (param) => {
        setPancingan(param)
        setTab("b")
    } 
    const handleSebelumnya = (param) => {
        setPancingan(param)
        setTab("a")
    }        
    const handleClickTabB = () => {
        if(tab!=="b"){
            setTab("b")
        }
    }    
    const fungsipancing = (hehe) => {
        setPancingan(!hehe)
        console.log(pancingan)
    }   
    useEffect(() => {
        window.scrollBy(0, 0)
        if(viavalen == "ya"){
            props.history.push('/helpkapal')
        }
        if(kembali == "ya"){
            props.history.push('/helpkapal')
        }
      }); 
  return (
    <>
    <CCard>
      <CCardBody>
      <div className="align-self-start">
        <div class="d-flex justify-content-center">
        <div onClick={handleClickTabA.bind(this)} className="item px-5 btn" >
        <LogoFormA tab={tab} />
        <span style={{display:"block"}}>
            Form A
        </span>
        </div>
        <div onClick={handleClickTabB.bind(this)} className="item px-5 btn" >          
        <LogoFormB tab={tab} />
        <span style={{display:"block"}}>
            Form B
        </span>
        </div>        
      </div> 
      </div> 
      <div>
          <IsiUtama defic={defic} kirimDefic={handleDefic} kembali={handleKembali} tab={tab} Viavalen={handleViavalen} kirimDefisiensi={handleDefisiensi} defisiensi={defisiensi} kirimInformasi={handleInformasi} informasi={informasi}s certificate={certificate} kirimCertificate={handleCertificate} juhuu={handleSebelumnya} jehee={handleSelanjutnya} calleback={fungsipancing} />
      </div>
      </CCardBody>
    </CCard>
    </>
  )
}

export default FormA
